.form {
  display: flex;
  /* border-top: 1px solid #D3D3D3; */
}

.input {
  border: none;
  border-radius: 0;
  padding: 5%;
  width: 80%;
  font-size: 1.2em;
  background-color: #2D2159;
  color: #fff;
}

input:focus, textarea:focus, select:focus{
  outline: none;
}

.sendButton {
  color: #fff !important;
  text-transform: uppercase;
  text-decoration: none;
  background: #8C1EFF;
  padding: 20px;
  display: inline-block;
  border: none;
  width: 20%;
  cursor: pointer
}